
body{
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.signBox{
  width: 138px;
  height: 138px;
  border: 1px solid #4091fb;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  position: absolute;
}
.infoStyle{
  text-align:center;
  line-height:86px;
  font-size:16px;
  font-weight:normal;
}
.textStyle{
  text-align:center;
  font-size:16px;
  font-weight:normal;
}
.delete{
  cursor: pointer;
  position:  absolute;
  right: -21px;
  top: -17px;
  border:  1px solid #4091fb;
  border-radius:  50%;
  width:  50px;
  width:  20px;
  height:  20px;
  line-height: 20px;
  text-align:  center;
  color: #4091fb;
  font-weight:normal;
}



