.multi-tab .el-tabs__header {
  background: #fff;
  margin-bottom: 20px;
  height: 126px;
}
.multi-tab .el-tabs__header .el-tabs__active-bar {
    height: 0;
}
.multi-tab .el-tabs__header .el-tabs__nav-scroll {
    padding: 0;
    line-height: 48px;
    text-align: center;
    padding-top: 38px;
}
.multi-tab .el-tabs__header .el-tabs__nav-scroll .el-tabs__nav {
      float: none;
}
.multi-tab .el-tabs__header .el-tabs__item.is-top {
    width: 300px;
    height: 48px;
    background: #fff;
    border: 1px solid #4091fb;
    cursor: pointer;
    outline: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    line-height: 48px;
    text-align: center;
    display: inline-block;
    vertical-align: bottom;
    padding: 0;
}
.multi-tab .el-tabs__header .el-tabs__item.is-active {
    border: none;
    width: 300px;
    height: 48px;
    background: #4091fb;
    color: #fff;
    cursor: pointer;
    outline: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    line-height: 48px;
    text-align: center;
    display: inline-block;
    vertical-align: bottom;
    padding: 0;
}
.multi-tab .el-tabs__nav-wrap::after {
  height: 0;
}



