
.sign-operation{
  position: absolute;
  bottom:10px;
  right:10px;
}
.btn-style{
  border:none;
  outline: none;
  display: inline-block;
  width: 40px;
  height: 40px;
  font-size:13px;
  text-align: center;
  line-height: 40px;
  border-radius: 50%;
}
.clear-sign{
  position: absolute;
  right: 0;
  top:-50px;
  border: 1px solid #4091fb;
  background: #fff ;
  color:#4091fb;
}
.sign-submit{
  background: #4091fb;
  color: #fff;
}
.container {
  width: 100%;
  height: 100%;
}
#canvasBox {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100%;
}
.greet {
  padding: 5px 0;
  font-size:16px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  line-height: 30px;
  width: 100%;
  text-align: center;
  color:#4091fb;
}
.greet select {
  font-size: 18px;
}
canvas {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  cursor: crosshair;
  border:1px dashed lightgray;
}
.image-box {
  width: 100%;
  height: 100%;
}
.image-box header{
  font-size: 18px;
}
.image-box img {
  max-width: 80%;
  max-height: 80%;
  margin-top: 50px;
  border: 1px solid gray;
}
.bg{
  width:100%;
  height:100%;
  background:#000;
  background:rgba(0,0,0,.3);
  position: absolute;
  left: 0;
  top:0;
  display:none;
}
.bg .center{
  width:200px;
  height:100px;
  background:#fff;
  position: absolute;
  left:0;
  top:0;
  bottom:0;
  right:0;
  margin:auto;
  -webkit-transform:rotate(90deg);
          transform:rotate(90deg);
  border-radius:5px;
}
.center_cen p{
  width:200px;
  text-align:center;
  padding-top:10px;
  font-size:18px;
  font-family:'SimHei';
}
#closeBtn{
  display:block;
  padding-top:30px;
  padding-left:40%;
}
.bg1{
  width:100%;
  height:100%;
  background:#000;
  background:rgba(0,0,0,.3);
  position: absolute;
  left: 0;
  top:0;
  display:none;
}
.bg1 .center1{
  padding:5px;
  width:200px;
  height:150px;
  background:#fff;
  position: absolute;
  left:0;
  top:0;
  bottom:0;
  right:0;
  margin:auto;

  border-radius:5px;
}
.center1 p{
  width:200px;
  padding-top:10px;
  font-size:18px;
  font-family:'SimHei';
  text-indent:2rem;
}
#closeBtn{
  display:block;
  padding-top:30px;
  padding-left:40%;
  color:#000;
}
.modal{
  width: 100%;
  height: 100%;
  position: absolute;
  left:0;
  right:0;
  bottom:0;
  top: 0;
  z-index: 2;
  background: rgba(0, 0, 0, 0.3);
  overflow-x: hidden;
  overflow-y: auto;
  display: none;
}
.modal-box{
  width:225px;
  height: 225px;
  background-color: #fff;
  position:fixed;
  left:0;
  right:0;
  top:0;
  bottom:0;
  margin:auto;
  border-radius: 5px;
}
.modal-header{
  padding: 15px;
  border-bottom: 1px solid #e5e5e5;
  font-size: 14px;
}
.modal-header h3{
  text-align: left;
  font-size: 14px;
}
.modal-body{
  width:200px;
  height:200px;
  background: url('../../../../static/images/Canvas/canvas-tips.png') no-repeat;
}
.modal-footer{padding: 10px;height:30px;
}
.modal-footer a{display: inline-block;border-radius: 4px;
}
.close{
  position: absolute;
  right: -10px;
  top: -10px;
  width: 30px;
  height: 30px;
  color: #fff;
  background: #ccc;
  border-radius: 50%;
  text-align: center;
  line-height: 30px
}




