
.ContractInfos .main {
  width: 1200px;
  /* height: 800px; */
  margin: 5px auto;
  background: #fff;
}
.ContractInfos .main .first {
    height: 60px;
    padding-left: 28px;
    border-bottom: 1px solid #ccc;
}
.ContractInfos .main .first span:nth-child(1) {
    font-size: 18px;
    font-weight: bold;
    color: #666;
}
.ContractInfos .main .second {
    line-height: 58px;
    border-bottom: 1px solid #ccc;
    height: 96px;
}
.ContractInfos .main .second .pic {
    padding: 23px 0 8px 15px;
    float: left;
}
.ContractInfos .main .second .text {
    padding: 23px 50px 0 0;
    float: right;
}
.ContractInfos .text span {
  color: #4091fb;
}
.ContractInfos .text strong {
  color: #666;
  font-weight: normal;
}
.ContractInfos .three {
  border-bottom: 1px solid #ccc;
  padding-bottom: 26px;
}
.ContractInfos .three .details {
  padding-top: 26px;
}
.ContractInfos .three .details a {
  color: #4091fb;
}
.ContractInfos .three .details strong:nth-child(1) {
  padding-left: 28px;
  font-weight: normal;
}
.ContractInfos .three .details span:nth-child(2) {
  color: #666;
}
.ContractInfos .three .details a:nth-child(3) {
  padding-left: 41px;
}
.ContractInfos .three .details a:nth-child(4) {
  padding-left: 20px;
  padding-right: 41px;
}
.ContractInfos .three .details strong:nth-child(5) {
  padding-left: 28px;
  font-weight: normal;
}
.ContractInfos .three .details strong:nth-child(7) {
  padding-left: 41px;
  font-weight: normal;
}
.ContractInfos .three .pics {
  padding-top: 20px;
  padding-left: 15px;
}
.ContractInfos .el-table--scrollable-x .el-table__body-wrapper {
  overflow: hidden !important;
}



