
.batchsigns[data-v-cbcba15c]{
    width: 1200px;
    margin:0 auto;
    background: #fff;
    overflow:hidden;
    height: 1160px;
}

/* 分页样式 */
.signing[data-v-cbcba15c]{
  clear: both;
  height: 880px;
  width: 100%;
  border-top: 1px solid #ddd;
}
.sign_left[data-v-cbcba15c]{
    /* margin-left: 160px;
    width: 60px;
    float: left; */
    width: 220px;
    height:822px;
    float: left;
}
#top[data-v-cbcba15c]{
    width: 47px;
    height: 20px;text-align: center;
    /* background: #ccc; */
    background: #0e90d2;
    margin-bottom: 36px;
}
#bottom[data-v-cbcba15c]{
    width: 47px;
    height: 20px;text-align: center;
    /* background: #ccc; */
    background: #0e90d2;
    margin-top: 36px;
}
.pagination[data-v-cbcba15c] {
    position: relative;
    top: 40px;
    float:right;
}
.pagination li a[data-v-cbcba15c]{
    width: 35px;
    height: 35px;
    border-radius: 50%;
    display: block;
    background:#dddddd;
    text-align: center;
    line-height: 35px;
    color: #666;
    margin-bottom: 3px;
    margin-top: 3px;
}
.pagination li[data-v-cbcba15c]:after
{
    content:"";
    font-weight:bold;
    border: 1px dashed #999;
    margin-left: 15px;
}
.pagination li[data-v-cbcba15c]:nth-child(11):after
{
    content:"";
    font-weight:normal;
    border: none;
    display: block;
    margin-top: -10px;
}
.pagination li a[data-v-cbcba15c]:hover{
    background:#0E90D2;
    color: #fff;
}
.pagination li.active a[data-v-cbcba15c]{
    background:#0E90D2;
    color:#fff;
}
.pagination li[data-v-cbcba15c]{
    margin-left: 6px;
}
.pagination p>a[data-v-cbcba15c]{
    color: #fff;
}
.pagination li.active a[data-v-cbcba15c]{
    background:#0E90D2;
    color:#fff;
}

/* 渲染合同样式 */
.sign_center[data-v-cbcba15c]{
    width: 595px;
    height: 822px;
    border: 1px solid #ccc;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    float: left;
    overflow: hidden;
    /*overflow-y: scroll;*/
    margin-top: 40px;
    margin-left: 40px;
}

/* 渲染合同图片 */
.contractImg[data-v-cbcba15c]{
    width: 100%;
    cursor: pointer;
}
.contractImg li[data-v-cbcba15c]{
    /* height: 844px; */
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size: 0;
}
.contractImg li img[data-v-cbcba15c]{
    width: 100%;
}

/* 提交样式 */
.sign_right[data-v-cbcba15c]{
    margin-top: 40px;
    float: left;
    margin-left: 25px;
}
.sign_right a[data-v-cbcba15c] {
    text-decoration: none;
}
.Tops[data-v-cbcba15c]{
    width: 100%;
    height: 80px;
    background:#4091fb;
    overflow:hidden;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
}
.nav[data-v-cbcba15c]{
    width: 1200px;
    margin: 0 auto;
    height: 80px;
    overflow:hidden;
    position: relative;
}
.nav .logo[data-v-cbcba15c]{
    float: left;
    margin-right: 70px;
}
.nav .logo img[data-v-cbcba15c]{
    margin-top: 18px;
}
.nav ul[data-v-cbcba15c]{
    margin-left: 80px;
    line-height: 80px;
}
.nav ul li[data-v-cbcba15c]{
    float: left;
    /* padding: 0 22px 0 22px; */
}
.nav ul li a[data-v-cbcba15c]{
    color: #fff;
}
.nav .btns[data-v-cbcba15c]{
    margin-left: 5px;
    /* line-height: 80px; */
}
.nav .btns li[data-v-cbcba15c]{
    float: left;
    width: 90px;
    height: 44px;
}
.nav .btns li[data-v-cbcba15c]:nth-child(1){
    float: left;
    width: 90px;
    height: 44px;
    background: #fff;
    line-height: 44px;
    border-radius: 5px;
    text-align: center;
    margin-top: 17px;
    margin-right: 24px;
}
.nav .btns li[data-v-cbcba15c]:nth-child(2){
    float: left;
    width: 90px;
    height: 44px;
    background: #fff;
    line-height: 44px;
    border-radius: 5px;
    text-align: center;
    margin-top: 17px;
}
.nav .btns li:nth-child(1) a[data-v-cbcba15c]{
    color: #4091fb;
}
.nav .btns li:nth-child(2) a[data-v-cbcba15c]{
    color: #4091fb;
}
.nav .btns li[data-v-cbcba15c]:nth-child(3){
    width: 116px;
    float: left;
    line-height: 80px;
    margin-left: 30px;
    text-align: center;
}
.nav .btns li:nth-child(3) img[data-v-cbcba15c]{
    vertical-align:middle;
}
.nav .btns li:nth-child(3) a[data-v-cbcba15c]{
    color: #fff;
    font-size: 14px;
}
.nav .btns li[data-v-cbcba15c]:nth-child(4){
    float: left;
    line-height: 80px;
    margin-left: -6px;
    text-align: center;
}
.nav .btns li:nth-child(4) img[data-v-cbcba15c]{
    vertical-align:middle;
}
.nav .btns li:nth-child(4) a[data-v-cbcba15c]{
    color: #fff;
    font-size: 14px;
}

/* buttons样式 */
.buttons[data-v-cbcba15c]{
    position: absolute;
    right: 10px;
    top: 20px;
}

/*签署步骤 共用css组件*/
.step[data-v-cbcba15c]{
  width: 890px;
  margin: 0 auto;
  height:120px;
  padding-top: 40px;
  padding-bottom: 30px;
}

/* 步骤条状态样式 */
.step ul li[data-v-cbcba15c]{
  float: left;
  width: 72px;
  height: 72px;
  border: 1px dashed #ccc;
  border-radius: 50%;
  padding: 5px;
  margin:0px 5px 0 5px;
}
.step ul li>i[data-v-cbcba15c]{
  text-align: center;
  line-height: 72px;
  display: block;
  font-size: 32px;
  background: #ccc;
  border-radius: 50%;
  color: #fff;
}
.step ul li> b[data-v-cbcba15c]{
  margin-top: 14px;
  text-align: center;
  display: block;
  font-weight: normal;
  color: #ccc;
}
.step ul li.active[data-v-cbcba15c]{
  border-color: #4091fb;
}
.step ul li.active i[data-v-cbcba15c]{
  background: #4091fb;
}
.step ul li.active b[data-v-cbcba15c]{
  color: #4091fb;
}
.step ul p[data-v-cbcba15c]{
  float: left;
  border-bottom:1px dashed #ccc;
  width: 60px;
  height: 1px;
  margin-top: 40px;
}



