.textInfo{
  clear: both;
  color:red;
  padding-top: 52px;
  font-size: 14px;
  text-align: center;
  /* padding-bottom: 70px; */
}
.btns{
  text-align: center;
}
.Topes{
    width: 100%;
    height: 80px;
    background:#4091fb;
    overflow:hidden;
}
.nav{
    width: 1200px;
    margin: 0 auto;
    height: 80px;
    overflow:hidden;
    position: relative;
}
.nav .logo{
    float: left;
    margin-right: 70px;
}
.nav .logo img{
    margin-top: 18px;
}
.nav ul{
    margin-left: 80px;
    line-height: 80px;
}
.nav ul li{
    float: left;
    /* padding: 0 22px 0 22px; */
}
.nav ul li a{
    color: #fff;
}
.nav .btns{
    margin-left: 5px;
    /* line-height: 80px; */
}
.nav .btns li{
    float: left;
    width: 90px;
    height: 44px;
}
.nav .btns li:nth-child(1){
    float: left;
    width: 90px;
    height: 44px;
    background: #fff;
    line-height: 44px;
    border-radius: 5px;
    text-align: center;
    margin-top: 17px;
    margin-right: 24px;
}
.nav .btns li:nth-child(2){
    float: left;
    width: 90px;
    height: 44px;
    background: #fff;
    line-height: 44px;
    border-radius: 5px;
    text-align: center;
    margin-top: 17px;
}
.nav .btns li:nth-child(1) a{
    color: #4091fb;
}
.nav .btns li:nth-child(2) a{
    color: #4091fb;
}
.nav .btns li:nth-child(3){
    width: 116px;
    float: left;
    line-height: 80px;
    margin-left: 30px;
    text-align: center;
}
.nav .btns li:nth-child(3) img{
    vertical-align:middle;
}
.nav .btns li:nth-child(3) a{
    color: #fff;
    font-size: 14px;
}
.nav .btns li:nth-child(4){
    float: left;
    line-height: 80px;
    margin-left: -6px;
    text-align: center;
}
.nav .btns li:nth-child(4) img{
    vertical-align:middle;
}
.nav .btns li:nth-child(4) a{
    color: #fff;
    font-size: 14px;
}
/* buttons样式 */
.buttons{
    position: absolute;
    right: 10px;
    top: 20px;
}
/*签署步骤 共用css组件*/
.step{
  width: 890px;
  margin: 0 auto;
  height:120px;
  padding-top: 40px;
  padding-bottom: 30px;
}
/* 步骤条状态样式 */
.step ul li{
  float: left;
  width: 72px;
  height: 72px;
  border: 1px dashed #ccc;
  border-radius: 50%;
  padding: 5px;
  margin:0px 5px 0 5px;
}
.step ul li>i{
  text-align: center;
  line-height: 72px;
  display: block;
  font-size: 32px;
  background: #ccc;
  border-radius: 50%;
  color: #fff;
}
.step ul li> b{
  margin-top: 14px;
  text-align: center;
  display: block;
  font-weight: normal;
  color: #ccc;
}
.step ul li.active{
  border-color: #4091fb;
}
.step ul li.active i{
  background: #4091fb;
}
.step ul li.active b{
  color: #4091fb;
}
.step ul p{
  float: left;
  border-bottom:1px dashed #ccc;
  width: 60px;
  height: 1px;
  margin-top: 40px;
}



