
@charset "UTF-8";
/*签署步骤 共用css组件*/
.step{
  width: 890px;
  margin: 0 auto;
  height:120px;
  padding-top: 40px;
  padding-bottom: 30px;
}
/* 步骤条状态样式 */
.step ul li{
  float: left;
  width: 72px;
  height: 72px;
  border: 1px dashed #ccc;
  border-radius: 50%;
  padding: 5px;
  margin:0px 5px 0 5px;
}
.step ul li>i{
  text-align: center;
  line-height: 72px;
  display: block;
  font-size: 32px;
  background: #ccc;
  border-radius: 50%;
  color: #fff;
}
.step ul li> b{
  margin-top: 14px;
  text-align: center;
  display: block;
  font-weight: normal;
  color: #ccc;
}
.step ul li.active{
  border-color: #4091fb;
}
.step ul li.active i{
  background: #4091fb;
}
.step ul li.active b{
  color: #4091fb;
}
.step ul p{
  float: left;
  border-bottom:1px dashed #ccc;
  width: 60px;
  height: 1px;
  margin-top: 40px;
}
.main {
  width: 1200px;
  margin: 0 auto;
}
.line {
  width: 100%;
  height: 1px;
  background: #ddd;
  margin-top: 20px;
  margin-bottom: 20px;
}
.common-top {
  width: 100%;
  height: 126px;
  background: #fff;
  margin-top: 20px;
  margin-bottom: 20px;
}
/* //列表样式 */
.list-body {
  background: #fff;
  padding: 20px 15px;
  padding-bottom: 60px;
}
/* 分页 */
.pagetion {
  padding-top: 50px;
  text-align: center;
}
/* 下拉选择 */
.el-select {
  width: 249px;
}
.common-top-tab {
  width: 602px;
  margin: 0 auto;
  padding-top: 38px;
}
.el-tabs__nav-scroll {
  font-size: 16px;
  color: #333;
}
#tab-first, #tab-second, #tab-third, #tab-fourth, #tab-five {
  font-size: 16px;
}
.el-table th > .cell {
  text-align: center;
  font-size: 14px;
}
#app {
  overflow: hidden;
}
.el-table__row .cell {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.el-tabs__header {
  background: #fff;
  margin: 0 auto;
}
.el-tabs__nav-scroll {
  line-height: 58px;
  padding: 0 25px;
}
.el-tabs__item {
  height: 58px;
  line-height: 58px;
}
.el-tabs__item.is-active {
  color: #4091fb;
  border-bottom: 2px solid #4091fb;
}
.el-table--scrollable-x .el-table__body-wrapper {
  overflow: hidden;
}
#contractTextInfo {
  display: inline-block;
  width: 237px;
  height: 38px;
  padding-left: 5px;
  padding-right: 5px;
  outline: none;
  border: 1px solid #dcdfe6;
  border-radius: 3px;
  margin-right: 20px;
}
#textInfo {
  display: inline-block;
  width: 237px;
  height: 38px;
  padding-left: 5px;
  padding-right: 5px;
  outline: none;
  border: 1px solid #dcdfe6;
  border-radius: 3px;
  margin-right: 20px;
}
.btn-default {
  width: 300px;
  height: 46px;
  background: #fff;
  border: 1px solid #4091fb;
  cursor: pointer;
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  line-height: 48px;
  text-align: center;
  display: inline-block;
  vertical-align: bottom;
}
.btn-active {
  width: 300px;
  height: 48px;
  background: #4091fb;
  color: #fff;
  cursor: pointer;
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  line-height: 48px;
  text-align: center;
  display: inline-block;
  vertical-align: bottom;
}
.currentStep .el-icon-location:before {
  color: #4091fb;
}
/* 头部title样式 */
.title-bg {
  background: url("/static/images/Common/title.png") no-repeat;
  height: 46px;
  margin-bottom: 15px;
  margin-top: 20px;
}
.title-name {
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  margin-left: 28px;
  margin-top: 10px;
  display: inline-block;
  letter-spacing: 2px;
  width: 204px;
}
.one {
  margin: 30px auto;
  width: 490px;
}
.one:hover {
  border: none;
}
.two {
  margin: 30px auto;
  width: 490px;
}
.two:hover {
  border: none;
}
.Mores .el-tabs__content {
  background: #fff;
}
.multi-tab .el-tabs__header {
  background: #fff;
  margin-bottom: 20px;
  height: 126px;
}
.multi-tab .el-tabs__header .el-tabs__active-bar {
    height: 0;
}
.multi-tab .el-tabs__header .el-tabs__nav-scroll {
    padding: 0;
    line-height: 48px;
    text-align: center;
    padding-top: 38px;
}
.multi-tab .el-tabs__header .el-tabs__nav-scroll .el-tabs__nav {
      float: none;
}
.multi-tab .el-tabs__header .el-tabs__item.is-top {
    width: 300px;
    height: 48px;
    background: #fff;
    border: 1px solid #4091fb;
    cursor: pointer;
    outline: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    line-height: 48px;
    text-align: center;
    display: inline-block;
    vertical-align: bottom;
    padding: 0;
}
.multi-tab .el-tabs__header .el-tabs__item.is-active {
    border: none;
    width: 300px;
    height: 48px;
    background: #4091fb;
    color: #fff;
    cursor: pointer;
    outline: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    line-height: 48px;
    text-align: center;
    display: inline-block;
    vertical-align: bottom;
    padding: 0;
}
.multi-tab .el-tabs__nav-wrap::after {
  height: 0;
}



