
.Topes{
    width: 100%;
    height: 80px;
    background:#4091fb;
    overflow:hidden;
}
.nav{
    width: 1200px;
    margin: 0 auto;
    height: 80px;
    overflow:hidden;
    position: relative;
}
.nav .logo{
    float: left;
    margin-right: 70px;
}
.nav .logo img{
    margin-top: 18px;
}
.nav ul{
    margin-left: 80px;
    line-height: 80px;
}
.nav ul li{
    float: left;
    /* padding: 0 22px 0 22px; */
}
.nav ul li a{
    color: #fff;
}
.nav .btns{
    margin-left: 5px;
    /* line-height: 80px; */
}
.nav .btns li{
    float: left;
    width: 90px;
    height: 44px;
}
.nav .btns li:nth-child(1){
    float: left;
    width: 90px;
    height: 44px;
    background: #fff;
    line-height: 44px;
    border-radius: 5px;
    text-align: center;
    margin-top: 17px;
    margin-right: 24px;
}
.nav .btns li:nth-child(2){
    float: left;
    width: 90px;
    height: 44px;
    background: #fff;
    line-height: 44px;
    border-radius: 5px;
    text-align: center;
    margin-top: 17px;
}
.nav .btns li:nth-child(1) a{
    color: #4091fb;
}
.nav .btns li:nth-child(2) a{
    color: #4091fb;
}
.nav .btns li:nth-child(3){
    width: 116px;
    float: left;
    line-height: 80px;
    margin-left: 30px;
    text-align: center;
}
.nav .btns li:nth-child(3) img{
    vertical-align:middle;
}
.nav .btns li:nth-child(3) a{
    color: #fff;
    font-size: 14px;
}
.nav .btns li:nth-child(4){
    float: left;
    line-height: 80px;
    margin-left: -6px;
    text-align: center;
}
.nav .btns li:nth-child(4) img{
    vertical-align:middle;
}
.nav .btns li:nth-child(4) a{
    color: #fff;
    font-size: 14px;
}
/* buttons样式 */
.buttons{
    position: absolute;
    right: 10px;
    top: 20px;
}
/*签署步骤 共用css组件*/
.step{
  width: 890px;
  margin: 0 auto;
  height:120px;
  padding-top: 40px;
  padding-bottom: 30px;
}
/* 步骤条状态样式 */
.step ul li{
  float: left;
  width: 72px;
  height: 72px;
  border: 1px dashed #ccc;
  border-radius: 50%;
  padding: 5px;
  margin:0px 5px 0 5px;
}
.step ul li>i{
  text-align: center;
  line-height: 72px;
  display: block;
  font-size: 32px;
  background: #ccc;
  border-radius: 50%;
  color: #fff;
}
.step ul li> b{
  margin-top: 14px;
  text-align: center;
  display: block;
  font-weight: normal;
  color: #ccc;
}
.step ul li.active{
  border-color: #4091fb;
}
.step ul li.active i{
  background: #4091fb;
}
.step ul li.active b{
  color: #4091fb;
}
.step ul p{
  float: left;
  border-bottom:1px dashed #ccc;
  width: 60px;
  height: 1px;
  margin-top: 40px;
}
.EnterprisePayments,
.EnterpriseRegisterSuccs {
  position: relative;
  clear: both;
  width: 1200px;
  margin: 20px auto;
  background: #fff;
  height: auto;
  padding-bottom: 30px;
}
.EnterprisePayments .payment-operate,
.EnterpriseRegisterSuccs .payment-operate {
  clear: both;
  border-top: 1px solid #ddd;
}
.EnterprisePayments .payment-operate .tips,
.EnterpriseRegisterSuccs .payment-operate .tips {
  width: 500px;
  text-align: center;
  font-weight: bold;
  color: #4091fb;
  font-size: 14px;
  margin: 0 auto;
  padding-top: 40px;
}
.EnterprisePayments .payment-operate .payment-content,
.EnterpriseRegisterSuccs .payment-operate .payment-content {
  padding: 40px 0;
  width: 570px;
  margin: 0 auto;
}
.EnterprisePayments .payment-operate .payment-content .payment-success,
.EnterpriseRegisterSuccs .payment-operate .payment-content .payment-success {
  width: 600px;
  margin: 0 auto;
  height: 180px;
  text-align: center;
}
.EnterprisePayments .payment-operate .payment-content .payment-success .image,
.EnterpriseRegisterSuccs .payment-operate .payment-content .payment-success .image {
  color: #4091fb;
  display: inline-block;
  text-align: left;
  vertical-align: middle;
}
.EnterprisePayments .payment-operate .payment-content .payment-success .success-tips,
.EnterpriseRegisterSuccs .payment-operate .payment-content .payment-success .success-tips {
  color: #333;
  display: inline-block;
  text-align: left;
  vertical-align: middle;
  margin-left: 30px;
}
.EnterprisePayments .payment-operate .payment-content .payment-success .success-tips span,
.EnterpriseRegisterSuccs .payment-operate .payment-content .payment-success .success-tips span {
  padding-top: 30px;
  color: #4091fb;
}
.EnterprisePayments .payment-operate .payment-content .sign-times,
.EnterpriseRegisterSuccs .payment-operate .payment-content .sign-times {
  width: 560px;
  margin: 0 auto;
}
.EnterprisePayments .payment-operate .payment-content .sign-times p,
.EnterpriseRegisterSuccs .payment-operate .payment-content .sign-times p {
  color: #4091fb;
  font-size: 16px;
  font-weight: bold;
}
.EnterprisePayments .payment-operate .payment-content .edit-btn,
.EnterpriseRegisterSuccs .payment-operate .payment-content .edit-btn {
  margin: 30px 0 0 150px;
}
.EnterprisePayments .payment-operate .payment-content .payment-info,
.EnterpriseRegisterSuccs .payment-operate .payment-content .payment-info {
  position: relative;
  padding-bottom: 25px;
  width: 550px;
}
.EnterprisePayments .payment-operate .payment-content .payment-info span,
.EnterpriseRegisterSuccs .payment-operate .payment-content .payment-info span {
  margin-right: 20px;
  font-size: 14px;
  color: #424242;
  width: 150px;
  display: inline-block;
  text-align: right;
}
.EnterprisePayments .enterprise-certificate,
.EnterpriseRegisterSuccs .enterprise-certificate {
  position: relative;
  width: 860px;
  height: 460px;
  margin: 0 auto;
  background: #f5f5f5;
  padding: 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.EnterprisePayments .enterprise-certificate .certificate-content,
.EnterpriseRegisterSuccs .enterprise-certificate .certificate-content {
  position: relative;
  width: 830px;
  background: #fff;
  height: 430px;
  margin: 0 auto;
}
.EnterprisePayments .enterprise-certificate .certificate-content .left-side,
.EnterpriseRegisterSuccs .enterprise-certificate .certificate-content .left-side {
  display: inline-block;
  width: 450px;
  height: 250px;
  vertical-align: bottom;
  margin-left: 30px;
  margin-top: 80px;
}
.EnterprisePayments .enterprise-certificate .certificate-content .left-side .card,
.EnterpriseRegisterSuccs .enterprise-certificate .certificate-content .left-side .card {
  width: 520px;
  height: 260px;
  display: inline-block;
}
.EnterprisePayments .enterprise-certificate .certificate-content .left-side .right-card-content,
.EnterpriseRegisterSuccs .enterprise-certificate .certificate-content .left-side .right-card-content {
  margin: 50px 0 0 175px;
}
.EnterprisePayments .enterprise-certificate .certificate-content .left-side .right-card-content .right-line span,
.EnterpriseRegisterSuccs .enterprise-certificate .certificate-content .left-side .right-card-content .right-line span {
  color: #666;
  display: inline-block;
  height: 40px;
}
.EnterprisePayments .enterprise-certificate .certificate-content .right-side,
.EnterpriseRegisterSuccs .enterprise-certificate .certificate-content .right-side {
  display: inline-block;
  vertical-align: bottom;
  width: 250px;
  height: 250px;
  margin-left: 30px;
}
.EnterprisePayments .enterprise-certificate .certificate-content .right-side .right-content img,
.EnterpriseRegisterSuccs .enterprise-certificate .certificate-content .right-side .right-content img {
  position: relative;
  left: 20px;
  top: 20px;
}
.EnterprisePayments .certification-book,
.EnterpriseRegisterSuccs .certification-book {
  clear: both;
  position: relative;
  margin: 30px auto 50px auto;
  width: 1000px;
  height: 200px;
  -webkit-box-shadow: 2px 2px 5px 5px #e3e2e2;
          box-shadow: 2px 2px 5px 5px #e3e2e2;
}
.EnterprisePayments .certification-book .bg-png,
.EnterpriseRegisterSuccs .certification-book .bg-png {
  width: 120px;
  height: 120px;
  display: inline-block;
  margin: 40px;
}
.EnterprisePayments .certification-book .download,
.EnterpriseRegisterSuccs .certification-book .download {
  display: inline-block;
  width: 760px;
  vertical-align: top;
  padding-top: 60px;
  border-left: 1px dot-dash #4091fb;
}
.EnterprisePayments .certification-book .download .tips,
.EnterpriseRegisterSuccs .certification-book .download .tips {
  color: #4091fb;
  font-size: 16px;
  font-weight: bold;
}
.EnterprisePayments .certification-book .download .tips span,
.EnterpriseRegisterSuccs .certification-book .download .tips span {
  color: #ff8a00;
}
.EnterprisePayments .certification-book .download .down-btn,
.EnterpriseRegisterSuccs .certification-book .download .down-btn {
  margin: 40px 0 30px 200px;
}
.EnterprisePayments .certification-book .download .down-btn a,
.EnterpriseRegisterSuccs .certification-book .download .down-btn a {
  width: 200px;
  height: 40px;
  display: inline-block;
  color: #4091fb;
  border: 1px solid #4091fb;
  border-radius: 5px;
  line-height: 40px;
  text-align: center;
}
.send-code {
  margin: 15px 0;
  font-size: 14px;
  color: #666;
}
.code-ruleForm {
  width: 500px;
  height: 300px;
}


