
.loading{
  width:128px;
  height:128px;
  position: fixed;
  left:0;
  top:0;
  right:0;
  bottom:0;
  margin:auto;
}
body{
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.signBox{
    width: 138px;
    height: 138px;
    border: 1px solid #4091fb;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    position: absolute;
}
.Pcontract .infoStyle{
    text-align:center;
    line-height:86px;
    font-size:16px;
    font-weight:normal;
}
.Pcontract .textStyle{
    text-align:center;
    font-size:16px;
    font-weight:normal;
}
.delete{
    cursor: pointer;
    position:  absolute;
    right: -21px;
    top: -17px;
    border:  1px solid #4091fb;
    border-radius:  50%;
    width:  50px;
    width:  20px;
    height:  20px;
    line-height: 20px;
    text-align:  center;
    color: #4091fb;
    font-weight:normal;
}



