
.view{
     width: 100%;
    height: 100%;
    background: #000;
    position: absolute;
    z-index: 4000;
    background-color: rgba(0,0,0,0.5);
    position: absolute;
    left:0;
    top:0;
}
.dialogbg{
  background:#000;
  background:rgba(0,0,0,.3);
  width:100%;
  height:1080px;
  position: absolute;
  left:0;
  top:0;
  z-index: 1999;
}
.el-upload-dragger{
  width: 680px !important;
  height: 154px !important;
  /* margin-left: 284px !important; */
  /* margin-top: -41px !important; */
}
.el-table tr:nth-child(odd){
  background:#fff;
}
.el-table tr:nth-child(even){
  background:#f5f5f5;
}
.el-table th>.cell{
  text-align: none;
}
.el-tabs__nav-scroll{
    font-size: 16px;
    color: #333;
}
#tab-first,#tab-second,#tab-third,#tab-fourth,#tab-five{
    font-size: 16px;
}
.el-table th>.cell{
  text-align: center;
}
.el-table__row .cell{
  overflow: hidden !important;
    text-overflow: ellipsis !important;
    white-space: nowrap !important;
}
#title{
  text-align:left !important;
}
.one:hover{
  border: 1px solid #4091fb;
}
.two:hover{
  border: 1px solid #4091fb;
}
#close{
  width:30px;
  height:30px;
  border-radius:50%;
  text-align:center;
  line-height:30px;
  display:block;
  background:#fff;
  color:#000;
}
.el-button--primary:focus{
  background: #eee;
  border-color: #95989d;
  color: #333;
}



